$min_width: 375px;
$max_width: 1500px;

.section{
  position: relative;

  &__title{
    font-family: 'IBM Plex Mono', monospace;
    @include fluid-type($min_width, $max_width, 30px, 60px);
    line-height:  calc(33px + (66 - 33) * ((100vw - 375px) / (1500 - 375)));
    font-weight: 600;

    color: #F8FAFE;
    &-center{
      text-align: center;
    }
  }
  &__desc{
    font-family: 'Raleway', sans-serif;
    @include fluid-type($min_width, $max_width, 18px, 27px);
    line-height:  calc(26px + (43 - 26) * ((100vw - 375px) / (1500 - 375)));
    color: #BCC2CE;

    margin-top: 25px;
  }
  &__content{
    margin-top: 90px;
  }
  &__footer{
    margin-top: 80px;
  }
  &-works{
    &::before{
      content: '';
      position: absolute;
      left: 45px;
      top: -350px;

      width: 566px;
      height: 470px;

      animation: fragment backwards 10s ease-in-out infinite;

      background: url("../img/coub-2.png") center center/contain no-repeat;
    }
  }
}

@media screen and (max-width: 1024px) {
  .section{
    &__desc{
      margin-top: 15px;
    }
    &__content{
      margin-top: 30px;
    }
    &__footer{
      margin-top: 45px;
    }
  }
}