html{
  scroll-behavior: smooth;
}

body{
  background-color: #000;

  color:#fff;
}


.container{
  width: 100%;
  max-width: 1500px;

  padding: 0 30px;

  margin: 0 auto;
  &-left{
    margin:0 0 0 calc(50% - 720px);
  }
}

.button{
  -webkit-appearance: none;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 80px;

  &-transparent{
    border: 1px solid rgba(248, 250, 254, 0.5);
    border-radius: 10px;

    font-family: 'IBM Plex Mono', monospace;
    font-weight: 500;
    font-size: 20px;
    color: #F8FAFE;

    background-color: transparent;
  }
  &-purple{
    background: #6640FF;
    border-radius: 10px;

    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 20px;
    color: #F8FAFE;
  }
  &-big{
    max-width: 474px;
  }

  &-center{
    margin: 0 auto;
  }
}

.home-page{
  &__wrapper{
    position: relative;
    overflow: hidden;
    &::before{
      content: '';
      position: absolute;
      right: 0;
      top: -200px;

      width: 880px;
      height: 860px;
      display: block;

      animation: fragment backwards 10s ease-in-out infinite;

      background: url("../img/hero-coub.png") center center/contain no-repeat;
    }
  }
  &__services{
    margin-top: 135px;
  }
  &__projects{
    margin-top: 170px;
  }
  &__technology{
    margin-top: 125px;
  }
  &__development{
    margin-top: 135px;
  }
  &__banner{
    margin-top: 160px;
  }
  &__works{
    margin-top: 130px;

    position: relative;
    z-index: 2;
  }
}

.checkbox{
  &__label{
    display: flex;
    align-items: flex-start;
    &:hover{
      cursor: pointer;
    }
  }
  &__input{
    display: none;
    &:checked ~ .checkbox__checkmark{
      background-color: #F2F2F2;
      &::before{
        opacity: 1;
      }
    }
  }
  &__checkmark{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 30px;
    height: 30px;

    border: 1px solid #F2F2F2;
    box-shadow: 0px 4px 10px rgba(67, 69, 128, 0.1);

    border-radius: 6px;
    position: relative;
    flex-shrink: 0;

    margin-right: 20px;

    transition: 0.3s background-color linear;

    &::before{
      content: '';
      position: absolute;
      left: calc(50% - 6px);
      top:calc(50% - 6px);

      width: 12px;
      height: 12px;
      display: block;

      background: url("../img/check.svg") center center/contain no-repeat;
      opacity: 0;

      transition: .3s opacity linear;
    }
  }
  &__text{
    font-family: 'Raleway', sans-serif;
    font-size: 18px;
    line-height: 28px;
    color: #BCC2CE;
    a{
      text-decoration: underline;
      text-underline-position: under;
    }
  }
}

*[data-animate]{
  opacity: 0;

  transition: .5s opacity linear, .75s all linear;
}

*[data-animate='fade-right']{
  transform: translateX(-100%);

  &.animated{
    transform: translateX(0);
  }
}

*[data-animate='fade-left']{
  transform: translateX(100%);

  &.animated{
    transform: translateX(0);
  }
}


*[data-animate='fade-top']{
  &.animated{
    animation: animateFadeTop .75s forwards;
  }
}


*[data-animate='fade-bottom']{
  transform: translateY(-100%);

  &.animated{
    transform: translateY(0);
  }
}

@keyframes animateFadeTop {
  0%{
    transform: translateY(100%);
  }
  100%{
    transform: translateY(0);

    opacity: 1;
  }
}

@media screen and (max-width: 1500px) {
  .container{
    &-left{
      margin: 0;

      padding: 0 30px;
    }
  }
}

@media screen and (max-width: 767px) {
  .button{
    height: 60px;
    font-size: 16px;
    &-purple{
      font-size: 16px;
    }
  }
  .home-page{
    &__wrapper{
      &::before{
        width: 550px;
        height: 550px;

        top: -150px;
        right: -100px;
      }
    }
    &__services{
      margin-top: 70px;
    }
    &__projects{
      margin-top: 55px;
    }
    &__technology{
      margin-top: 94px;
    }
    &__works{
      margin-top: 80px;
    }
    &__development{
      margin-top: 60px;
    }
    &__banner{
      margin-top: 85px;
    }
  }
}