.header{
  position: absolute;
  z-index: 10;
  left: 0;
  top: 45px;
  width: 100%;
  &__container{
    padding: 0 30px;

    width: 100%;
    max-width: 1410px;

    margin: 0 auto;
  }
  &__burger{
    display: none;
  }
  &__row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-block{
      display: flex;
      align-items: center;
    }
    &-mobile{
      display: none;
    }
  }
  &__logo{
    height: 96px;
  }
  &__nav{
    margin-left: 96px;
  }
  &__phone{
    margin-left: 110px;
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
    &__item{
      & + &{
        margin-left: 45px;
      }
    }
    &__link{
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 500;
      font-size: 21px;

      color:#F8FAFE;
    }
  }
  &-mobile{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    opacity: 0;
    z-index: -1;

    transition: .5s all linear;
    &__wrapper{
      padding: 35px 30px;

      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;

      background-color: #000;

      position: relative;

      &::before{
        content: '';

        position: absolute;
        left: -50px;
        top: 0px;

        width: 260px;
        height: 285px;

        display: block;

        animation: fragment backwards 10s ease-in-out infinite;

        background: url("../img/coub-2.png") center center/contain no-repeat;
      }
      &::after{
        content: '';

        position: absolute;
        right: 0px;
        bottom: 0px;

        width: 125px;
        height: 125px;

        display: block;

        animation: fragment_reverse backwards 10s ease-in-out infinite;

        background: url("../img/coub-3.png") center center/contain no-repeat;
      }
    }
    &__logo{
      width: 50px;
      height: 50px;
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__close{
      width: 50px;
      height: 50px;

      &-img{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__content{
      margin-top: 55px;
      &-block + &-block{
        margin-top: 75px;

        position: relative;
        &::before{
          content: '';
          position: absolute;
          left: 0;

          top: -37px;
          width: 100%;
          height: 1px;

          background-color: #2F2D3E;
        }
      }
    }
    &__block{
      & + &{
        margin-top: 15px;
      }
      &-title{
        font-family: 'Raleway',sans-serif;
        font-size: 16px;
        color: #BCC2CE;
      }
      &-content{
        font-family: 'Raleway', sans-serif;
        font-size: 18px;
        color: #F8FAFE;

        margin-top: 5px;

        max-width: 250px;
      }
    }
    &__footer{
      margin-top: auto;

      padding-top: 40px;
    }
    &__button{
      max-width: 315px;
      height: 60px;
    }
  }
}

.logo{
  height: 100%;
  width: auto;
  &__img{
    width: 100%;
    height: 100%;

    object-fit: contain;
  }
}

.phone{
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 600;
  font-size: 26px;
  color: #F8FAFE;
}

.language{
  &-list{
    display: flex;
    align-items: center;
    &__item{
      position: relative;

      color:#BCC2CE;
      &::before{
        content: '';
        position: absolute;
        left: calc(50% - 4px);
        bottom: -16px;

        display: none;
        width: 8px;
        height: 8px;

        border-radius: 50%;

        background: #F8FAFE;
      }
      &.active{
        color:#fff;
        &::before{
          display: block;
        }
      }
      & + &{
        margin-left: 20px;
      }
    }
    &__link{
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 500;
      font-size: 21px;
      color:inherit;

      text-transform: uppercase;
    }
  }
}

.burger{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  width: 40px;
  height: 11px;

  &__item{
    flex-shrink: 0;

    width: 100%;
    height: 1px;

    background: #F8FAFE;
  }
}

@media screen and (max-width: 1200px) {
  .header{
    &__phone,&__nav{
      margin-left: 30px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .header{
    top: 30px;
    &__row{
      &-mobile{
        display: flex;
      }
      &-nav{
        position: fixed;

        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        background-color: #000000;
        z-index: 99;
      }
    }
    &__phone,&__language,&__nav{
      display: none;
    }
    &__logo{
      height: 60px;
    }
    &__burger {
      display: block;
    }
    &-mobile{
      &.active{
        z-index: 99;
        opacity: 1;
      }
    }
    &-nav{
      &__list{
        align-items: flex-start;
        flex-direction: column;
      }
      &__item{
        & + &{
          margin-left: 0;

          margin-top: 30px;
        }
      }
      &__link{
        font-size: 24px;
      }
    }
  }
}