.section{
  &-works{
    &__footer{
      position: relative;

      &::before{
        content: '';
        position: absolute;
        right: 0;
        top: -200px;

        width: 290px;
        height: 264px;
        display: block;

        animation: fragment_reverse backwards 10s ease-in-out infinite;

        background: url("../img/coub-techonology.png") center center/contain no-repeat;
      }
    }
  }
}

.work{
  &-list{
    position: relative;
    &__item{
      &:hover{
        .work-item__img{
          opacity: 1;
          z-index: 3;
        }
      }
    }
  }
  &-item{
    &__wrapper{
      transition: .5s background-color linear;


      &:hover{
        background-color: rgba(#181722,.7);
      }
    }
    &__content{
      display: grid;
      grid-template-columns: 450px 60px auto;

      column-gap: 65px;

      padding: 50px 0;

      border-top: 1px solid #414048;

    }
    &__title{
      font-family: 'IBM Plex Mono',monospace;
      font-weight: 600;
      font-size: 28px;
      line-height: 36px;
      color: #F8FAFE;
    }
    &__link{
      font-family: 'Raleway', sans-serif;
      font-size: 19px;
      font-weight: 500;
      line-height: 33px;
      color: #8A66FF;
    }
    &__text{
      font-family: 'IBM Plex Mono', monospace;
      font-weight: 600;
      font-size: 23px;
      line-height: 30px;
      color: #BCC2CE;
    }
    &__img{
      position: absolute;
      top: 10%;
      right: calc(50% - 750px);
      height: 80%;

      width: 333px;

      border-radius: 20px;

      overflow: hidden;


      opacity: 0;
      z-index: -1;

      transition: 0.3s all linear;

      img{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    &__block{
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
  &-list{
    &__item:last-child{
      .work-item__content{

        border-bottom: 1px solid #414048;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .work{
    &-item{
      &__img{
        right: 30px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .work{
    &-item{
      &__content{
        grid-template-columns: auto 60px;
        gap: 10px
      }
      &__img{
        display: none;
      }
      &__block{
        &:nth-child(3){
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .section{
    &-works{
      &::before{
        display: none;
      }
      &__footer{
        &::before{
          display: none;
        }
      }
      &__title{
        color:#3C3B43
      }
    }
  }
  .work{
    &-item{
      &__content{
        padding: 20px 0;
      }
      &__title{
        font-size: 16px;
        line-height: 20px;
      }
      &__text{
        font-size: 16px;
        line-height: 24px;
      }
      &__link{
        font-size: 13px;
        line-height: 33px;
      }
    }
  }
}