.section{
  &-technology{
    position: relative;

    &::before{
      content: '';

      position: absolute;
      width: 200%;
      height: 1000px;
      left: 0;
      top: -200px;
      z-index: 1;
      display: block;

      background:url("../img/technology-bg.svg") left top/contain no-repeat;
    }

    &__wrapper{
      position: relative;
      z-index: 2;
    }
  }
}

.technology{
  &-grid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    gap: 150px;
    &__item{
      position: relative;

      & + &{
        &::before{
          content: '';
          position: absolute;
          left: -75px;
          top: 0;
          height: 100%;
          width: 1px;
          display: block;

          background-color: #23212E;
        }
      }
    }
  }
  &-item{
    &__title{
      font-family: "IBM Plex Mono", monospace;
      font-weight: 600;
      font-size: 43px;
      color: #F8FAFE;
    }
    &__content{
      margin-top: 30px;

      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));

      gap: 30px;
    }
  }
  &-list{
    &__text,&__title{
      font-family: 'Raleway', sans-serif;
      font-size: 23px;
      line-height: 38px;
      color: #BCC2CE;
    }
    &__title{
      font-weight: 700;
    }
    &__item{
      list-style: disc;
      margin-left: 20px;
      &-title{
        list-style: none;
        margin-left: 0;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .technology{
    &-grid{
      gap: 20px;
      &__item{
        & + &{
          &::before{
            left: -10px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .technology{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 30px;
      &__item{
        & + &{
          &::before{
            display: none;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .section{
    &-technology{
      &::before{
        content: '';

        position: absolute;
        width: 700px;
        height: 600px;
        left: unset;
        right: -300px;
        top: -300px;
        z-index: 1;
        display: block;

        background:url("../img/technology-bg-m.svg") center right/contain no-repeat;
      }
      &__header{
        position: relative;

        &::before{
          content: '';
          position: absolute;
          left: -150px;
          top: -70px;

          width: 285px;
          height: 240px;
          display: block;

          background: url("../img/coub-2.png") center center/contain no-repeat;
        }
      }
    }
  }
  .technology{
    &-grid{
      grid-template-columns: minmax(0,1fr);
      gap: 0;
      border-bottom: 1px solid rgba(65, 64, 72, 0.73);

    }
    &-item{
      &__title{
        font-size: 23px;
      }
      &__header{
        border-top: 1px solid rgba(65, 64, 72, 0.73);

        padding: 18px 15px;

        display: flex;
        align-items: center;
        justify-content: space-between;

        &::after{
          content: '';
          width: 15px;
          height: 15px;
          display: block;

          background: url("../img/arrow-down.svg") center center/contain no-repeat;
        }

        &.active{
          &::after{
            transform:rotate(180deg);
          }
        }
      }
      &__content{
        margin: 0;

        padding:0 15px 10px 15px;

        border-bottom: 1px solid rgba(65, 64, 72, 0.73);

        display: none;

        &.active{
          display: block;
        }
      }
    }
    &-list{
      &__text,&__title{
        font-size: 15px;
        line-height: 25px;
      }
    }
  }
}