$min_width: 375px;
$max_width: 1500px;


.section {
  &-hero {
    &__grid {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));

      gap: 135px;
    }

    &__title {
      max-width: 710px;
    }

    &__desc {
      margin-top: 30px;

      max-width: 610px;
    }

    &__button {
      margin-top: 80px;
    }

    &__wrapper {
      padding-top: 200px;

      position: relative;
    }

    &__slider {
      display: flex;
      align-items: center;

      position: relative;

      &::before {
        content: '';
        position: absolute;
        left: -220px;
        top: -20px;

        width: 670px;
        height: 410px;
        display: block;

        background: url("../img/hero-slider-bg.svg") center center/contain no-repeat;
      }
    }

    &__footer {
      margin-top: 90px;

      position: relative;

      &::before {
        content: '';
        position: absolute;

        top: calc(50% - 140px);
        left: -100px;

        width: 285px;
        height: 285px;
        display: block;

        animation: fragment_reverse backwards 10s ease-in-out infinite;

        background: url("../img/hero-coub-second.png") center center/contain no-repeat;
      }
    }
  }
}

.hero {
  &-slider {
    &__item {
      width: 535px;
      height: 270px;

      &-wrapper {
        width: 100%;
        height: 100%;

        background: rgba(26, 25, 29, 0.78);
        box-shadow: 0px 123px 212px #000000;
        border-radius: 27px;

        display: flex;
        align-items: center;

        overflow: hidden;

        padding: 30px 25px;
      }

      &-img {
        width: 235px;
        height: 235px;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

      &-content {
        margin-left: 20px;
      }

      &-title {
        font-family: 'IBM Plex Mono', monospace;
        font-weight: 700;

        @include fluid-type($min_width, $max_width, 28px, 70px);
        color: #F8FAFE;
      }

      &-desc {
        margin-top: 5px;

        font-family: 'IBM Plex Mono', monospace;
        @include fluid-type($min_width, $max_width, 16px, 21px);
        color: #F8FAFE;
      }
    }

    &__navigation {
      display: flex;
      align-items: center;

      &-bar {
        width: 340px;
        height: 2px;

        position: relative;

        margin: 0 30px;

        background: #E6E6E6;

        .swiper-pagination-progressbar-fill {
          background-color: #6640FF;
          height: 2px;
        }
      }

      &-current, &-last {
        font-family: 'IBM Plex Sans', sans-serif;
        font-size: 22px;
        color: #F8FAFE;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .section {
    &-hero {
      &__slider {
        width: calc(100% + 30px);
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .section {
    &-hero {
      &__grid {
        grid-template-columns: minmax(0, 1fr);
        gap: 60px;
      }

      &__slider {
        &::before {
          left: unset;
          right: -300px;
          top: -330px;

          width: 1000px;
          height: 660px;

          background: url("../img/hero-slider-bg-m.svg") center right/contain no-repeat;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .section {
    &-hero {
      &__wrapper {
        padding-top: 150px;
      }

      &__button {
        margin-top: 35px;
      }

      &__footer {
        margin-top: 30px;

        &::before{
          display: none;
        }
      }

      &__slider {
        &::before {
          top: -400px;
          right: -500px;
        }
      }
    }
  }
  .hero {
    &-slider {
      &__item {
        width: 315px;
        height: 200px;

        &-img {
          width: 120px;
          height: 120px;
        }

        &-wrapper {
          box-shadow: none;

          padding: 25px 15px;
        }
      }
    }
  }
}


@keyframes fragment {
  0% {
    -webkit-transform: scale(1) translateX(0);
    transform: scale(1) translateX(0);
  }
  50% {
    -webkit-transform: scale(1.1) translateX(-10%);
    transform: scale(1.1) translateX(-10%);
  }
  100% {
    -webkit-transform: scale(1) translateX(0);
    transform: scale(1) translateX(0);
  }
}

@keyframes fragment_reverse {
  0% {
    -webkit-transform: scale(1) translateX(0);
    transform: scale(1) translateX(0);
  }
  50% {
    -webkit-transform: scale(0.9) translateX(10%);
    transform: scale(0.9) translateX(10%);
  }
  100% {
    -webkit-transform: scale(1) translateX(0);
    transform: scale(1) translateX(0);
  }
}