.home-page__wrapper {
  position: relative;
  overflow: hidden;
}

.home-page__wrapper::before {
  content: '';
  position: absolute;
  right: 0;
  top: -200px;
  width: 880px;
  height: 860px;
  display: block;
  animation: fragment backwards 10s ease-in-out infinite;
  background: url("../img/hero-coub.png") center center/contain no-repeat;
}

.home-page__services {
  margin-top: 135px;
}

.home-page__projects {
  margin-top: 170px;
}

.home-page__technology {
  margin-top: 125px;
}

.home-page__development {
  margin-top: 135px;
}

.home-page__banner {
  margin-top: 160px;
}

.home-page__works {
  margin-top: 130px;
  position: relative;
  z-index: 2;
}

.checkbox__label {
  display: flex;
  align-items: flex-start;
}

.checkbox__label:hover {
  cursor: pointer;
}

.checkbox__input {
  display: none;
}

.checkbox__input:checked ~ .checkbox__checkmark {
  background-color: #F2F2F2;
}

.checkbox__input:checked ~ .checkbox__checkmark::before {
  opacity: 1;
}

.checkbox__checkmark {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border: 1px solid #F2F2F2;
  box-shadow: 0px 4px 10px rgba(67, 69, 128, 0.1);
  border-radius: 6px;
  position: relative;
  flex-shrink: 0;
  margin-right: 20px;
  transition: 0.3s background-color linear;
}

.checkbox__checkmark::before {
  content: '';
  position: absolute;
  left: calc(50% - 6px);
  top: calc(50% - 6px);
  width: 12px;
  height: 12px;
  display: block;
  background: url("../img/check.svg") center center/contain no-repeat;
  opacity: 0;
  transition: .3s opacity linear;
}

.checkbox__text {
  font-family: 'Raleway', sans-serif;
  font-size: 18px;
  line-height: 28px;
  color: #BCC2CE;
}

.checkbox__text a {
  text-decoration: underline;
  text-underline-position: under;
}

*[data-animate] {
  opacity: 0;
  transition: .5s opacity linear, .75s all linear;
}

*[data-animate='fade-right'] {
  transform: translateX(-100%);
}

*[data-animate='fade-right'].animated {
  transform: translateX(0);
}

*[data-animate='fade-left'] {
  transform: translateX(100%);
}

*[data-animate='fade-left'].animated {
  transform: translateX(0);
}

*[data-animate='fade-top'].animated {
  animation: animateFadeTop .75s forwards;
}

*[data-animate='fade-bottom'] {
  transform: translateY(-100%);
}

*[data-animate='fade-bottom'].animated {
  transform: translateY(0);
}

@keyframes animateFadeTop {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@media screen and (max-width: 1500px) {
  .container-left {
    margin: 0;
    padding: 0 30px;
  }
}

@media screen and (max-width: 767px) {
