}

.header__container {
  padding: 0 30px;
  width: 100%;
  max-width: 1410px;
  margin: 0 auto;
}

.header__burger {
  display: none;
}

.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__row-block {
  display: flex;
  align-items: center;
}

.header__row-mobile {
  display: none;
}

.header__logo {
  height: 96px;
}

.header__nav {
  margin-left: 96px;
}

.header__phone {
