.header-mobile {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: -1;
  transition: .5s all linear;
}

.header-mobile__wrapper {
  padding: 35px 30px;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #000;
  position: relative;
}

.header-mobile__wrapper::before {
  content: '';
  position: absolute;
  left: -50px;
  top: 0px;
  width: 260px;
  height: 285px;
  display: block;
  animation: fragment backwards 10s ease-in-out infinite;
  background: url("../img/coub-2.png") center center/contain no-repeat;
}

.header-mobile__wrapper::after {
  content: '';
  position: absolute;
  right: 0px;
  bottom: 0px;
  width: 125px;
  height: 125px;
  display: block;
  animation: fragment_reverse backwards 10s ease-in-out infinite;
  background: url("../img/coub-3.png") center center/contain no-repeat;
}

.header-mobile__logo {
  width: 50px;
  height: 50px;
}

.header-mobile__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-mobile__close {
  width: 50px;
  height: 50px;
}

.header-mobile__close-img {
  width: 100%;
  height: 100%;
