}

.header-mobile__content {
  margin-top: 55px;
}

.header-mobile__content-block + .header-mobile__content-block {
  margin-top: 75px;
  position: relative;
}

.header-mobile__content-block + .header-mobile__content-block::before {
  content: '';
  position: absolute;
  left: 0;
  top: -37px;
  width: 100%;
  height: 1px;
