.section{
  &-project{
    background: url("../img/project-gradient-bg.png") top center/contain no-repeat;

    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 215px;

      &-block{
        & + &{
          margin-top: 140px;
        }
      }
    }
    &__header{
      margin-bottom: 100px;
    }
  }
}

.project{
  &-card{
    &:hover{
      .project-card__info{
        opacity: 1;
      }
    }
    &__img{
      height: 740px;

      border-radius: 20px;

      overflow: hidden;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    &__content{
      margin-top: 40px;
    }
    &__title{
      font-family: 'IBM Plex Mono', monospace;
      font-weight: 600;
      font-size: 35px;
      line-height: 46px;

      color: #F8FAFE;
    }
    &__desc{
      font-family: 'Raleway', sans-serif;
      font-size: 21px;
      line-height: 31px;
      color: #BCC2CE;


      margin-top: 15px;

      max-width: 510px;
      width: 100%;
    }
    &__header{
      position: relative;

      border-radius: 20px;
      overflow: hidden;
    }
    &__info{
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;

      width: 100%;
      height: 100%;
      display: block;

      background: rgba(0,0,0,.4);

      opacity: 0;

      border-radius: 18px;
      overflow: hidden;

      transition: .3s opacity linear;

      &::before{
        content: '';
        position: absolute;
        bottom: -10px;
        right: 0;

        display: block;
        width: 100%;
        height: 100%;

        background: url("../img/card-bg.svg") center center/cover no-repeat;
      }
    }
  }
  &-info{
    height: 100%;
    position: relative;
    z-index: 3;
    &__wrapper{
      height: 100%;
      padding: 35px 40px;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &__copy{
      display: flex;
      align-items: center;
      justify-content: center;

      width: 72px;
      height: 72px;

      border-radius: 50%;

      background-color: #EFF0FF;

      margin-left: auto;
    }
    &__tags{
      &-block + &-block{
        margin-top: 15px;
      }
    }
  }
  &-tags{
    &__wrapper{
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin: -10px;
    }
    &__item{
      margin: 10px;
    }
  }
  &-tag{
    width: max-content;
    &__wrapper{
      padding: 12px 16px;
      background: rgba(102, 64, 255, 0.8);
      border-radius: 9px;
    }
    &__text{
      font-family: 'IBM Plex Sans',sans-serif;
      font-weight: 600;
      font-size: 15px;
      color: #EFF0FF;
    }
  }
}

@media screen and (max-width: 1500px) {
  .project{
    &-card{
      &__img{
        height: 70vh;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .section {
    &-project {
      &__grid {
        gap: 50px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .section{
    &-project{
      background: none;
      &__grid{
        grid-template-columns: minmax(0,1fr);
        &-block {
          & + &{
            margin-top: 50px;
          }
        }
        &-item{
          &:nth-child(2){
            order: 1;
          }
          &:nth-child(1){
            order: 2;
          }
        }
      }
      &__header{
        margin-bottom: 44px;
      }
    }
  }
  .project{
    &-card{
      &__img{
        height: 315px;
      }
      &__title{
        font-size: 24px;
        line-height: 31px;
      }
      &__desc{
        font-size: 15px;
        line-height: 21px;

        margin-top: 10px;
      }
    }
    &-info{
      &__wrapper{
        padding: 15px;
      }
    }
    &-tags{
      &__wrapper{
        margin: -5px;
      }
      &__item{
        margin: 5px;
      }
    }
  }
}