$min_width: 375px;
$max_width: 1500px;

.section{
  &-services{
    &__desc{
      max-width: 726px;
      width: 100%;

      margin: 27px auto 0 auto;

      text-align: center;
    }
    &__content{
      margin-top: 96px;

      position: relative;

      &::before{
        content: '';
        position: absolute;
        left: calc(50% - 100%);
        top: calc(50% - 100%);

        width: 200%;
        height: 200%;
        display: block;

        background: url("../img/service-bg.svg") center center/contain no-repeat;
      }
    }
  }
}

.services{
  &-card{
    &__wrapper{
      padding: 26px 62px 66px 62px;

      border-radius: 20px;

      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      border:1px solid;

      border-image-slice: 1;
      border-image-source: linear-gradient(to bottom, #7A75B1, rgba(#5F5D6E,0));

      height: 445px;
    }
    &__img{
      width: 190px;
      height: 190px;

      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__content{
      margin-top: 10px;
    }
    &__title{
      font-family: 'IBM Plex Mono',monospace;
      font-weight: 600;
      font-size: 27px;
      line-height: 35px;
      color: #F8FAFE;
      text-align: center;

      max-width: 310px;
      width: 100%;

      margin: 0 auto;
    }
    &__desc{
      margin-top: 20px;

      font-family: 'Raleway', sans-serif;
      font-weight: 400;
      font-size: 22px;
      line-height: 33px;
      text-align: center;
      color: #BCC2CE;
    }
  }
}

@media screen and (max-width: 600px) {
 .services{
   &-card{
     &__wrapper{
       padding: 25px 32px;
     }
     &__img{
       width: 140px;
       height: 140px;
     }
     &__title{
       font-size: 24px;
       line-height: 31px;
     }
     &__desc{
       margin-top: 15px;

       font-size: 16px;
       line-height: 24px;
     }
   }
 }

}