.case{
  &-page{
    position: relative;

    overflow: hidden;
    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 2000px;
      height: 1000px;
      display: block;

      background: url("../img/case-header-bg.png") center center/contain no-repeat;
    }
    &__banner{
      margin-top: 115px;
    }
  }
  &__wrapper{
    padding-top: 200px;
  }
  &__name{
    font-family: 'IBM Plex Mono', monospace;
    font-weight: 600;
    font-size: 38px;

    color: #6B53FF;

    padding-top: 40px;
  }
  &__title{
    margin-top: 45px;

    font-family: 'IBM Plex Mono', monospace;
    font-weight: 700;
    font-size: 60px;
    line-height: 65px;

    color: #F8FAFE;
  }
  &__text{
    font-family: 'Raleway', sans-serif;
    font-size: 26px;
    line-height: 40px;

    color: #F8FAFE;
  }
  &-block{
    position: relative;
    z-index: 3;
  }

  &-details{
    &::before{
      content: '';
      position: absolute;
      width: 2000px;
      left: -70%;
      top: -10%;
      height: 1000px;
      display: block;

      z-index: 2;

      background: url("../img/case-details-bg.png") center center/contain no-repeat;
    }
  }

  &__about{
    margin-top: 85px;

    position: relative;

    z-index: 2;

    &::before{
      content: '';
      position: absolute;
      width: 2000px;
      bottom: -110%;
      right: -400px;
      height: 1000px;
      display: block;

      background: url("../img/case-about-bg.png") center center/contain no-repeat;
    }
  }
  &-header{
    position: relative;


    &__wrapper{
      display: grid;
      grid-template-columns: 580px auto;
      gap: 90px;
    }
    &__block{
      display: flex;
      flex-direction: column;
    }
    &__img{
      width: 100%;
      height: 564px;

      display: flex;
      align-items: center;
      justify-content: center;

      &-item{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &-about{
    &__wrapper{
      display: grid;
      grid-template-columns: 2fr 1fr;

      gap: 170px;

      position: relative;

      &::before{
        content: '';

        position: absolute;
        bottom: 0;
        right: 0;

        width: 290px;
        height: 264px;
        display: block;

        background: url("../img/coub-3.png") center center/contain no-repeat;
      }
    }
    &__block{
      &-title{
        font-family: 'IBM Plex Mono', monospace;
        font-style: normal;
        font-weight: 600;
        font-size: 18px;

        text-transform: uppercase;

        color: #BCC2CE;
      }
      &-content{
        margin-top: 20px;
      }
    }
    &__block + &__block{
      margin-top: 70px;
    }
    &__button{
      max-width: 474px;
    }
  }
  &-block{
    margin-top: 120px;

    &_top{
      &-big{
        margin-top: 200px;
      }
      &-middle{
        margin-top: 105px;
      }
      &-small{
        margin-top: 85px;
      }
    }
    &__images{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 30px;
    }
    &__image{
      width: 100%;
      height: 805px;
      &-full{
        grid-column-end: span 2;
      }
      &-small{
        height: 585px;
      }

      img{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    &__title{
      font-family: 'IBM Plex Mono', monospace;
      font-style: normal;
      font-weight: 500;
      font-size: 49px;

      color: #F8FAFE;
    }
    &__text{
      margin-top: 25px;

      &-small{
        max-width: 1110px;
      }
      &-big{
        max-width: 1240px;
      }
    }
    &__header,&__content{
      position: relative;
      z-index: 3;
    }
    &__content{
      margin-top: 85px;
    }
  }
}

.indicators{
  &-grid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    gap: 230px;

    &__number{
      font-family: 'IBM Plex Mono', monospace;
      font-style: normal;
      font-weight: 600;
      font-size: 47px;
      color: #F8FAFE;
    }
    &__text{
      margin-top: 20px;

      font-family: 'IBM Plex Mono', monospace;
      font-weight: 600;
      font-size: 28px;
      line-height: 36px;

      color: #F8FAFE;
    }
  }
}

@media screen and (max-width: 1024px) {
  .case{
    &__wrapper{
      padding-top: 105px;
    }
    &-header{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);

        gap: 35px;
      }
      &__block{
        &:nth-child(2){
          grid-row-start: 1;
        }
        &:nth-child(1){
          grid-row-start: 2;
        }
      }
      &__img{
        height: auto;
        width: calc(100% + 60px);
        transform: translateX(-30px);
      }
    }
    &__about{
      margin-top: 45px;
      &::before{
        display: none;
      }
    }
    &__text{
      font-size: 18px;
      line-height: 30px;
    }
    &__name{
      padding-top: 0;
      font-size: 22px;
      line-height: 29px;
    }
    &__title{
      font-size: 30px;
      line-height: 31px;

      margin-top: 15px;
    }
    &-about{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
        gap: 35px;
        &::before{
          top: -50px;
          right: -30px;

          width: 200px;
          height: 185px;
        }
        &-block{
          &:nth-child(2){
            grid-row-start: 1;
          }
          &:nth-child(1){
            grid-row-start: 2;
          }
        }
      }
      &__block{
        & + &{
          margin-top: 35px;
        }
      }
    }
    &-block{
      margin-top: 50px;
      &_top{
        &-middle{
          margin-top: 20px;
        }
      }
      &__images{
        grid-template-columns: minmax(0,1fr);
      }
      &__image{
        height: auto;
        &-full{
          grid-column-end: auto;
        }
      }
      &__title{
        font-size: 28px;
      }
      &__text{
        margin-top: 10px;
      }
      &__content{
        margin-top: 40px;
      }
    }
  }

  .indicators{
    &-grid{
      grid-template-columns: minmax(0,1fr);
      gap: 30px;
      &__text{
        font-size: 18px;
        line-height: 24px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .case{
    &-details{
      &::before{
        width: 100%;
        height: 100%;
        top:-50px;
        left: unset;
        right: -60px;
        z-index: 2;

        background: url("../img/case-details-bg-m.png") center center/contain no-repeat;

      }
    }
    &-block{
      &__image{
        height: 168px;
      }
    }
  }
}