.footer{
  margin-top: 170px;
  &__logo{
    width: 96px;
    height: 96px;
  }
  &__row{
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding: 60px 0;

    & + &{
      border-top: 1px solid #302F37;
    }
    &-block{
      max-width: 400px;
      &__title{
        font-family: 'Raleway', sans-serif;
        font-size: 20px;
        line-height: 30px;

        color: #BCC2CE;
      }
      &__text{
        margin-top: 10px;

        font-family: 'Raleway', sans-serif;
        font-size: 25px;
        color: #F8FAFE;
      }
      &__link{
        text-decoration: underline;
        text-underline-position: under;
      }
    }
  }
}

.social{
  &-list{
    display: flex;
    align-items: center;
    &__item{
      & + &{
        margin-left: 30px;
      }
    }
    &__link{
      display: flex;
      align-items: center;
      justify-content: center;

      width: 64px;
      height: 64px;
      border-radius: 50%;
      background: #BCC2CE;

      transition: 0.3s background-color;
      &:hover{
        background: #6640FF;
        svg{
          path{
            fill: #fff;
          }
        }
      }
    }
    &__img{
      height: 29px;
      height: 29px;

      display: flex;
      align-items: center;
      justify-content: center;
      svg{
        width: 100%;
        height: 100%;
        path{
          transition: .3s all;

        }
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .footer{
    &__row{
      flex-wrap: wrap;
      margin: -10px 0;
      &-block{
        margin: 10px 0;

        text-align: center;
        &_full{
          width: 100%;
          max-width: 100%;
          flex: 100%;

          order: 3;

          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
        }
        &_social{
          order: 2;
        }
        &_logo{
          order: 1;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .footer{
    margin-top: 20px;
    &__row{
      padding: 36px 0;
      &-block{
        &__text{
          font-size: 18px;
          line-height: 32px;
        }
        &__title{
          font-size: 16px;
        }
      }
    }
    &__logo{
      height: 60px;
      width: 60px;
    }
  }

  .social{
    &-list{
      &__item{
        & + &{
          margin-left: 20px;
        }
      }
      &__link{
        width: 50px;
        height: 50px;
      }
      &__img{
        width: 22px;
        height: 22px;
        object-fit: contain;
      }
    }
  }
}