.banner{
  &__wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 670px;

    background: #000000;
    box-shadow: 0px 4px 9px rgba(67, 69, 128, 0.1);
    border-radius: 36px;

    background: url("../img/banner-bg.jpg") center center/cover no-repeat;

    position: relative;

    overflow: hidden;

    &::before{
      content: '';
      position: absolute;
      top: -250px;
      left: -150px;

      width: 626px;
      height: 626px;
      display: block;

      animation: fragment backwards 10s ease-in-out infinite;

      background: url("../img/banner-coub.png") center center/contain no-repeat;
    }
  }
  &__img{
    width: 210px;
    height: 210px;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__content{
    margin-top: 15px;
  }

  &__title{
    font-family: 'IBM Plex Mono', monospace;
    font-style: normal;
    font-weight: 600;
    font-size: 47px;
    line-height: 60px;

    text-align: center;

    color: #F8FAFE;

    max-width: 575px;
    width: 100%;

    margin: 0 auto;
  }

  &__footer{
    margin-top: 50px;
  }

  &__button{
    width: 275px;
    height: 90px;
  }
}

@media screen and (max-width: 600px) {
  .banner{
    &__wrapper{
      padding: 22px 33px 60px 33px;

      height: 434px;
      background: url("../img/banner-bg-m.jpg") center center/cover no-repeat;

      &::before{
        width: 200px;
        height: 200px;

        top: -50px;
        left: -50px;
      }
    }
    &__img{
      width: 130px;
      height: 130px;
    }
    &__content{
      margin-top: 5px;
    }
    &__title{
      font-size: 30px;
      line-height: 35px;
    }
    &__footer{
      margin-top: 50px;
    }
    &__button{
      width: 170px;
      height: 60px;

      background-color: transparent;
      border: 1px solid #F8FAFE;

      color:#F8FAFE;
    }
  }
}