.popup{
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  z-index: 10;

  overflow: hidden;
  &-form{
    &__wrapper{
      display: flex;
      align-items: center;
      flex-direction: column;

      padding: 75px 0;

      width: 100%;
      height: 100%;

      background-color: #000000;

      overflow-y: auto;
      overflow-x: hidden;

      position: relative;
      &::after{
        content: '';
        position: absolute;
        right: -150px;
        top: -150px;
        width: 890px;
        height: 890px;
        display: block;

        animation: fragment backwards 10s ease-in-out infinite;


        background: url("../img/hero-coub.png") center center/contain no-repeat;
      }
      &::before{
        content: '';
        position: absolute;
        width: 730px;
        height:100%;

        z-index: 2;

        left: 0;
        top: 0;

        display: block;

        background: url("../img/popup-bg.png") bottom left/contain no-repeat;
      }
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;

      position: relative;
      z-index: 3;

      max-width: 1566px;
      width: 100%;

      padding: 0 30px;

      margin: 0 auto;
    }
    &__content{
      position: relative;
      z-index: 3;

      margin-top: 115px;

      max-width: 970px;
      width: 100%;
      padding: 0 30px;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 80px 110px;
    }
    &__block{
      &-title{
        font-family: "IBM Plex Mono", sans-serif;
        font-weight: 600;
        font-size: 18px;
        color: #BCC2CE;
      }
      &-content{
        margin-top: 15px;

        position: relative;
      }
    }
    &__error{
      position: absolute;
      bottom: calc(-100% + 12px);
      left: 0;

      font-family: 'Raleway', sans-serif;
      font-weight: 400;
      font-size: 18px;

      color: #E7153A;
    }
    &__input{
      -webkit-appearance: none;
      background: none;

      border: none;

      border-bottom: 1px solid #F8FAFE;
      width: 100%;

      padding-bottom: 12px;
      outline: none;

      color: #F8FAFE;
      font-family: 'Raleway',sans-serif;
      font-size: 23px;

      transition: 0.3s border-color linear;

      &.error{
        border-bottom-color: #E7153A;
      }

      &::placeholder{
        color:#F8FAFE;

        opacity: 1;
      }

      &-textarea{
        resize: none;
        height: 170px;
      }
    }
    &__desc{
      font-family: 'Raleway', sans-serif;
      font-size: 23px;
      line-height: 34px;

      color: #F8FAFE;

      margin-bottom: 15px;
    }
    &__button{
      max-width: 300px;
      height: 100px;
    }
    &__file{
      &-input{
        display: none;
      }
      &-label{
        &:hover{
          cursor: pointer;
        }
      }
      &-text{
        font-family: 'IBM Plex Mono',monospace;
        font-size: 16px;

        text-align: center;
        text-decoration-line: underline;
        text-underline-position: under;

        color: #BCC2CE;
      }
    }
  }
  &__logo{
    width: 92px;
    height: 92px;
  }
  &__title{
    font-family: 'IBM Plex Mono',monospace;
    font-weight: 600;
    font-size: 28px;
    line-height: 36px;
    text-align: center;

    color: #F8FAFE;
  }
  &__close{
    width: 90px;
    height: 90px;

    svg{
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 767px) {
  .popup{
    &__logo{
      order: 1;

      width: 60px;
      height: 60px;
    }
    &__close{
      order: 2;

      width: 50px;
      height: 50px;
    }
    &__title{
      width: 100%;
      order: 3;

      margin-top: 55px;

      text-align: left;

      font-size: 24px;
      line-height: 31px;
    }
    &-form{
      &__header{
        flex-wrap: wrap;
      }
      &__error{
        font-size: 16px;
      }
      &__wrapper{
        padding: 30px 0;

        &::after{
          width: 470px;
          height: 470px;

          top: -50px;
        }
        &::before{
          left: unset;
          right: 0;
          width: 100%;
          background: url("../img/popup-bg-m.png") top right/contain no-repeat;
        }
      }
      &__content{
        margin-top: 50px;
      }
      &__grid{
        grid-template-columns: minmax(0,1fr);
        gap: 50px;
      }
      &__button{
        height: 60px;
      }
      &__block{
        &-title{
          display: none;
        }
        &-content{
          margin-top: 0;
        }
      }
      &__input{
        font-size: 16px;
        line-height: 21px;

        &-textarea{
          height: 120px;
        }
      }
    }
  }

  .checkbox{
    &__text{
      font-size: 14px;
      line-height: 22px;
    }
  }
}