.development{
  &-grid{
    &__wrapper{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 55px;
    }
  }
  &-item{
    &__header{
      width: 100%;
      height: 355px;

      position: relative;

      padding: 25px 30px;

      display: flex;
      align-items: flex-end;
    }
    &__img{
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;

      width: 100%;
      height: 100%;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    &__title{
      font-family: 'IBM Plex Mono', monospace;
      font-size: 48px;
      letter-spacing: -0.02em;
      color: #FFFFFF;

      position: relative;
      z-index: 2;
    }
    &__content{
      margin-top: 40px;
    }
    &__desc{
      font-family: "Raleway", sans-serif;
      font-weight: 400;
      font-size: 21px;
      line-height: 33px;
      color: #BCC2CE;

      max-width: 550px;
    }
    &__footer{
      margin-top: 35px;

    }
    &__link{
      font-family: 'Raleway', sans-serif;
      font-weight: 400;
      font-size: 21px;

      color: #8A66FF;
    }
  }
}

@media screen and (max-width: 991px) {
  .development{
    &-grid{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
        gap: 50px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .development{
    &-item{
      &__header{
        height: 165px;

        padding: 15px;
      }
      &__title{
        font-size: 22px;
      }
      &__content{
        margin-top: 15px;
      }
      &__desc{
        font-size: 15px;
        line-height: 20px;
      }
      &__footer{
        margin-top: 15px;
      }
      &__link{
        font-size: 15px;
      }
    }
  }
}